import axios from 'axios'
import { getLoginToken } from '@/utils/token'
import { Message, Modal, Notice } from 'iview'
import loginTip from '@/components/tip/index'
// axios
const service = axios.create({
    baseURL:'http://47.100.57.39:7001/',   // api  base_url
    timeout: 5000 // 
})

// request
service.interceptors.request.use(
  config => {
      
      config.headers['x-token'] = getLoginToken()
      console.log('',config)
      //reutrn 
      return config
  },
  error => {
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// response 
service.interceptors.response.use(
  response => {
    /**
     * 10000 
     */
    const res = response.data
    if(response.data.code == 10000) 
    {
        Message.error('')
        console.log(res,'')
    }
    if(response.data.code == 30000)
    {
      Modal.confirm({ 
          title:'',
          duration:60,
          closable:true,
          render:(h)=>{
            return h(loginTip,{

            })
          },
          onOk:()=>{
            console.log('onOK')
            window.location.href='//'+window.location.host +'/public/manager/index.html#/login'
          }
        })

        console.log(res,'')
    }
    return response
  },
  error => {
console.log(error)
  }
)

export default service



